import React, { Component } from "react";
import CategoryList from "./CategoryList";
import Navi from "./Navi";
import ProductList from "./ProductList";
// ? Baka bir componenti kullanmak iin ncelikle import adiyoruz, ardndan return ierisinde aryoruz(Direkt return iindede <"___" /> sayesinde otomatik import edip kullanabiliriz)
// ! Reactstrap'i kullanmak iin import etmemiz gerekiyor
// ! Bir componentten dieine data tamak iin Props'u kullanrz
import { Container, Row, Col } from "reactstrap";
import alertify from "alertifyjs";

export default class App extends Component {
  state = { currentCategory: "", products: [], cart: [] };

  componentDidMount() {
    this.getProducts();
  }

  changeCategory = (category) => {
    this.setState({ currentCategory: category.categoryName });
    this.getProducts(category.id);
  };

  // ? categorilere tklandnda o kategorinin rnlerinin listelenmesi iin ncelikle categoryListteki categoryId 'imizi kullandk. Fakat categoriye tklaynca getProducts'mzn yeniden almad  iin eventimizi yollammz lazm
  getProducts = (categoryId) => {
    let url = "http://localhost:3000/products";
    if (categoryId) {
      url += "?categoryId=" + categoryId;
    }
    fetch(url)
      .then((response) => response.json())
      .then((data) => this.setState({ products: data }));
  };

  addToCart = (product) => {
    let newCart = this.state.cart;
    var addedItem = newCart.find((c) => c.product.id === product.id);
    if (addedItem) {
      addedItem.quantity++;
    } else {
      newCart.push({ product: product, quantity: 1 });
    }
    this.setState({ cart: newCart });
    alertify.success(product.productName + "added to cart!");
  };

  removeFromCart = (product) => {
    let newCart = this.state.cart.filter((c) => c.product.id !== product.id);
    this.setState({ cart: newCart });
  };

  render() {
    let productInfo = { title: "ProductList" };
    let categoryInfo = { title: "CategoryList" };
    return (
      <div>
        {/* <h2>Hello React !!! </h2> */}
        {/* Navi'yi direkt <Navi /> eklindede yazabilirim */}
        {/* <Navi></Navi> */}
        <Container>
          {/* <div> */}
          <Navi removeFromCart={this.removeFromCart} cart={this.state.cart} />
          {/* </div> */}

          <Row>
            <Col xs="2" role="button">
              {/* //* Datamz tamak datay tayacamz yere gelip title veririz. (title yerine baka bir isimde verebiliriz fakat dier tarafta ayn isimle armamz gerekir)  */}
              {/* <CategoryList title="Category List" /> */}
              {/* <CategoryList title={titleCategory} /> */}
              <CategoryList
                currentCategory={this.state.currentCategory}
                changeCategory={this.changeCategory}
                info={categoryInfo}
              />
            </Col>
            <Col xs="9">
              {/* <ProductList title="Product List" /> */}
              {/* <ProductList title={titleProduct} /> */}
              <ProductList
                products={this.state.products}
                addToCart={this.addToCart}
                currentCategory={this.state.currentCategory}
                info={productInfo}
              />
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

// ! stersek category ve product listimizi bir deiken ierisinde oluturup, ordanda alabiliriz
// * Bu deikenleri okumak iin ise direkt title'n iine gidip {} ierisinde deiikenimizi arabiliriz
