import React, { Component } from "react";
import { ListGroup, ListGroupItem } from "reactstrap";

export default class CategoryList extends Component {
  // ! App js 'deki category list'imi okumak iin bir constructor oluturuz ,
  // ! ardndan "super" diyerek props'mz component'imize tayoruz. en son ise state oluturuyoruz. Sonra bu state'i {this.state.counter} eklinde kullanyoruz
  // ? en son ise armak istediimiz yere gidip this.props deyip title diyerek aryoruz
  // constructor(props) {
  // super(props);
  state = {
    categories: [],
  };
  // ? componentDidMount fonk. sayesinde componentimin yerletiini bildirip, datalrm dolduruyorum
  componentDidMount() {
    this.getCategories();
  }
  // }
  // *=======================
  // ! app.js'de encapsulation yntemimizi uyguladktan sonra burada divimizin iine girip {this.props.info.__deikenismi__} eklinde aryoruz
  // ! changeCategory diye yeni bir deiken oluturduk ve bunu eventimizde ardk

  getCategories = () => {
    fetch("http://localhost:3000/categories")
      .then((response) => response.json())
      .then((data) => this.setState({ categories: data }));
  };

  render() {
    return (
      <div>
        {/* <h3>{this.props.title}</h3> */}
        <h3>{this.props.info.title}</h3>
        {/* <h3>{this.state.counter}</h3> */}

        <ListGroup>
          {/* //! yukarda yazm olduumuz categories'im ierisinde ki bilgileri getirmek iin */}
          {this.state.categories.map((category) => (
            <ListGroupItem
              active={
                category.categoryName === this.props.currentCategory
                  ? true
                  : false
              }
              onClick={() => this.props.changeCategory(category)}
              key={category.id}
            >
              {category.categoryName}
            </ListGroupItem>
          ))}
        </ListGroup>
        {/* <h4>{this.props.currentCategory}</h4> */}
      </div>
    );
  }
}
